<template>
    <div class="detail">
        <action-bar title='优惠券详情'></action-bar>
        <div class="detail-box">
            <div class="detail-item">
                <div>已使用|已领取: </div>
                <div>{{ data.receiveInfo }}</div>
            </div>
            <div class="detail-item">
                <div>成交转化率(已使用/已领取): </div>
                <div>{{ data.rate }}</div>
            </div>
            <div class="detail-item">
                <div>优惠券名称: </div>
                <div>{{ data.title }}</div>
            </div>
            <div class="detail-item">
                <div>优惠券类型: </div>
                <div>{{ getType(data.type) }}</div>
            </div>
            <div class="detail-item">
                <div>券的面额: </div>
                <div>{{ data.amount }}</div>
            </div>
            <div class="detail-item">
                <div>使用条件: </div>
                <div>{{ data.minAmount }}</div>
            </div>
            <div class="detail-item">
                <div>发行张数: </div>
                <div>{{ data.issuedNumber }}</div>
            </div>
            <div class="detail-item">
                <div>每人限额: </div>
                <div>{{ data.limitPerson }}</div>
            </div>
            <div class="detail-item">
                <div>开始时间: </div>
                <div>{{ data.beginTime }}</div>
            </div>
            <div class="detail-item last-bottom">
                <div>结束时间: </div>
                <div>{{ data.endTime }}</div>
            </div>
        </div>
    </div>
</template>
<script>
import { businessCouponInfo } from '@/http/api'
export default {
  name: '',
  components: {

  },
  data () {
    return {
      data: {}
    }
  },
  computed: {
    id () {
      return this.$route.query.id
    }
  },
  methods: {
    getType (type) {
      if (type === '整单') {
        return '店铺满减券'
      } else if (type === '品类') {
        return '品类券'
      } else if (type === '商品') {
        return '商品券'
      }
    },
    getDetail () {
      businessCouponInfo({
        couponId: this.id
      }).then(res => {
        this.data = res.data
      }).catch(res => {
        this.$toast(res.msg)
      })
    }
  },
  mounted () {
    this.getDetail()
  }
}
</script>
<style lang="scss" scoped>
.detail {
    background-color: #F2F2F2;
    width: 100vw;
    height: 100vh;
}

.detail-box {
    margin: .625rem;
    padding: .625rem;
    background-color: white;
    border-radius: .375rem;
}

.detail-item {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1.25rem;
}

.last-bottom {
    margin-bottom: 0;
}
</style>
